#builderContainer {
  width: 480px;
  background-color: white;
  height: 630px;
  font-family: 'Open Sans', sans-serif;
}
#innerBuilderContainer {
  width: 90%;
  height: 100%;
  margin: auto;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#builderHeading {
  font-size: 20pt;
  color: rgba(44, 62, 80, 255);
  text-align: left;
  width: 100%;
  padding-top: 25px;
  margin: 0;
}

@media screen and (max-width: 1020px) {
  #builderContainer {
    width: 100%;
    height: auto;
  }
}


