#previewContainer {
	width:520px;
	background-color: #e1e4e7;
	height: 630px;
}
#innerPreviewContainer {
	width:430px;
	height:300px;
	margin-left:auto;
	margin-right:auto;
	-ms-flex-align: center;
	    align-items: center;
	margin-top: 130px;
}
#previewTitle {
	margin:0;
	text-align: right;
	width:100%;
	color: rgba(149, 165, 166, 255);
	font-family: 'Open Sans', sans-serif;
	font-size:13pt;
	padding-bottom:4px;
}
#previewTopSection {
	width:100%;
	height:100px;
	background-color: #2c3e50;
	position: relative;
}
#previewName {
	width: calc(100% - 125px);
	font-family: 'Lora', serif;
	color:white;
	padding-left: 25px;
	position: absolute;
	bottom: -5px;
	left: 0;
	font-size:20pt;
}

@media screen and (max-width: 1020px) {
	#previewContainer  {
		width:100%;
		height: auto;
	}
	#innerPreviewContainer {
		margin-top: 50px;
		margin-bottom: 50px;
		margin-right:auto;
		margin-left:auto;
		width:90%;
		max-width:430px;
		height:auto;
	}
}


