#previewDiv {
	width:100%;
	background-color: white;
	padding-bottom:25px;
	border-bottom: 2px solid #9a9a9a;
}
#innerPreviewDiv {
	width:90%;
	margin-right:auto;
	margin-left:auto;
	padding-top: 19px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.infoPreviewPartContainerfull {
	width:100%;
}
.infoPreviewPartContainerhalf {
	width:50%;
}
.infoPreviewPartContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.labelPreviewInfoContainer {
	width:75px;
	border-bottom: 1px solid #dbe0e2;
	margin-top:1px;
	min-height: 23px;
}
.infoPreviewPartLabel {
	width:100%;
	font-family: 'Open Sans', sans-serif;
	font-size: 8.5pt;
	color: rgba(52, 73, 94, 255);
	text-transform: uppercase;
	text-align: left;
	margin-bottom: 0px;
	margin-top: 13px;
}
.textPreviewInfoContainer {
	width: calc(100% - 75px);
	border-bottom: 1px solid #dbe0e2;
	margin-top: 8px;
	min-height: 23px;
}
.infoPreviewPartText {
	width:100%;
	font-family: 'Lora', serif;
	font-size: 13.5pt;
	color: rgba(0, 0, 0, 255);
	text-align: left;
}

@media screen and (max-width: 420px) {
	.infoPreviewPartContainerhalf, .labelPreviewInfoContainer, .textPreviewInfoContainer{
		width:100%;
	}
	.noLabelPreviewinfoPart > div:first-child {
		display:none;
	}
}


