.builderButtonContainer {
  width: 49%;
  margin-left: auto;
  margin-right: auto;
  min-width: 210px;
  text-align: center;
}
.buttonBuilder {
  border-width: 0px 0px 2px 0px;
  border-radius: 5px;
  width: 195px;
  height: 42px;
  color: white;
  margin-top: 25px;
  font-size: 15pt;
}
#builderAvatarButton {
  background-color: #627b8b;
  border-color:  #566B79;
}
#builderSubmitButton {
  background-color: #3498db;
  border-color: #3289C4;
  margin-bottom: 30px;
}


