.builderSectionContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width:100%;
}
.builderSectionTitle {
  font-size: 9pt;
  color: rgba(176, 184, 188, 255);
  text-align: left;
  text-transform: uppercase;
  width: 100%;
  border-bottom: 1px solid #dbe0e2;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left:12px;
  margin-right:12px;
}
.builderInputOuterContainer {
  width: 49%;
  min-width: 210px;
  margin: auto;
}
.inputInputInnerContainer {
  width: 190px;
  margin: auto;
  margin-top: 10px;
}
.builderInputlabel {
  font-size: 9pt;
  color: rgba(44, 62, 80, 255);
  text-align: left;
  text-transform: uppercase;
  width: 190px;
  font-weight: bold;
}
.builderInputText {
  font-family: 'Lora', serif;
  font-size: 14pt;
  color: rgba(0, 0, 0, 255);
  text-align: left;
  width: 180px;
  height :35px;
  border-radius: 4px;
  border :1px solid #000;
  margin-right:10px;
}
.builderInputText::-webkit-input-placeholder {
  color: #CACACA;
}
.builderInputText::-moz-placeholder {
  color: #CACACA;
}
.builderInputText:-ms-input-placeholder {
  color: #CACACA;
}
.builderInputText[type=text] {
  padding-left: 10px;
}


