@import url('https://fonts.googleapis.com/css?family=Lora|Open+Sans');

#HCardBuilder {
  width:100%;
  max-width:1000px;
  min-width:320px;
  margin-left: auto;
  margin-right: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top:15px;
  margin-bottom:15px;
}

@media screen  and (max-width: 1020px) {
  #HCardBuilder {
    margin-top:0px;
    margin-bottom:0px;
  }
}


