 #avatarContainer {
	border-style: solid;
	border-width: 1px 1px 3px 1px;
	border-color: grey;
	background-color: white;
	position: absolute;
	bottom: -17px;
	right: 10px;
	overflow: hidden;
	width:83px;
	height:103px;
  }
#avatar {
   position: absolute;
   left: 50%;
   -webkit-transform: translateX(-50%);
   -ms-transform: translateX(-50%);
           transform: translateX(-50%);
}




